<template>
	<div id="RentalService">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">发布出租房</div>
        <!--<div class="van-nav-bar__right color_40a3f3">-->
          <!--5/30-->
        <!--</div>-->
      </div>
    </div>
    <div class="login_box">
      <van-list v-model="loading" :finished="finished" @load="getRentalList">
        <div style="margin-top: 5px" v-for="(item,index) in RentalList">
          <div class="text_center font_12 color_8b8b8b">
            <span>{{item.addtime}}</span>
          </div>
          <div class="background_color_ffffff font_12" style="margin: 0 10px">
            <div class="text_right" style="border-bottom: 1px solid #eeeeee;padding: 6px">
              <span style="float: left;margin-top: 4px;">{{item.title}}</span>
              <img src="static/img/icon_shanchu_wdwx.png" height="16px" @click="delService(item)"/>
            </div>
            <div style="padding: 6px">
              <span class="color_8b8b8b">{{item.content}}</span>
            </div>
            <div style="padding: 6px;text-align: right">
              <span class="color_c10804" @click="toRoomInfo(item)">查看详情</span>
            </div>
          </div>
        </div>
      </van-list>
      <div class="text_center background_color_ffffff" style="position: fixed;width: 100%;bottom: 0;padding: 5px;height: 30px;line-height: 30px;">
        <img src="static/img/icon_tj1.png" width="15px" style="vertical-align: middle;"/>
        <span class="font_12 color_c10804" @click="toAddRental">添加发布</span>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'RentalService',
		data() {
			return {
			  RentalList:[],
        userId:localStorage.getItem('userId'),
        loading: false,
        finished: false,
        pageNum: 1,
			}
		},
    mounted() {
      this.getRentalList();
    },
		methods:{
      delService(dataMap){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:dataMap.samecityid
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/deletesamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.getRentalList();
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      toRoomInfo(dataMap){
        this.$store.commit('setRoomInfo', dataMap);
        this.$router.push('roomInfo1');
        // this.$router.push({path:'roomInfo',query:{data:dataMap}});
      },
      toAddRental(){
        this.$router.push('addRental');
      },
      getRentalList(){
        this.loading = true;
        let $this = this;
        let obj = {
          userid: $this.userId,
          type:3,
          pageNum:this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.RentalList = $this.RentalList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_box{
    margin-top: 50px;
    margin-bottom: 45px;
  }
</style>



